 microorganismos protectores (probióticos). 2. Fortalecimiento de la flora intestinal, protección de la barrera mucosa intestinal haciendo que funcione de manera adecuada el sistema de defensas, lo cual previene la aparición de alergias, disminuye la respuesta inflamatoria, infecciones respiratorias, diarreas y otitis. 3. Salud oral: Desarrollo de los maxilares y el sistema masticatorio, avance mandibular, adecuada función y posición de la lengua que facilita el equilibrio en el rostro. El ejercicio de los músculos masticadores y faciales disminuye hasta el 50% los indicadores de mala oclusión dentaria. 4. El proceso de contracción del útero y recogimiento de los órganos que se desplazan durante el embarazo, se ve beneficiado dada la cascada de hormonas que son estimuladas al amamantar. 5. Disminución del riesgo de padecer depresión posparto, hipertensión, osteoporosis, cáncer de mama u ovario."}})])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('figure',{staticClass:"d-block d-lg-none mb-5",attrs:{"data-aos":"zoom-in"}},[_c('img',{staticClass:"m-auto",staticStyle:{"width":"550px"},attrs:{"src":require("@/assets/curso/tema2/img18m.svg"),"alt":"1. Protección frente a infecciones dado el gran contenido de microorganismos protectores (probióticos). 2. Fortalecimiento de la flora intestinal, protección de la barrera mucosa intestinal haciendo que funcione de manera adecuada el sistema de defensas, lo cual previene la aparición de alergias, disminuye la respuesta inflamatoria, infecciones respiratorias, diarreas y otitis. 3. Salud oral: Desarrollo de los maxilares y el sistema masticatorio, avance mandibular, adecuada función y posición de la lengua que facilita el equilibrio en el rostro. El ejercicio de los músculos masticadores y faciales disminuye hasta el 50% los indicadores de mala oclusión dentaria. 4. El proceso de contracción del útero y recogimiento de los órganos que se desplazan durante el embarazo, se ve beneficiado d