_v("Este diagnóstico se desarrolla desde el embarazo a través de la placenta, donde el feto recibe el oxígeno que necesita para desarrollarse. Por ello necesita gran cantidad de glóbulos rojos que sean capaces de transportar la mayor cantidad de oxígeno posible. Una vez llega a la etapa del nacimiento, el recién nacido consigue el oxígeno a través de sus pulmones y allí se convierte en un proceso mucho más fácil. Es por esta razón, que muchos de los glóbulos rojos sobran en el recién nacido y su organismo debe eliminarlos. El proceso de eliminación se lleva a cabo liberando la hemoglobina contenida en los glóbulos rojos, la cual es encargada de transportar oxígeno y se convierte mediante procesos metabólicos normales, en bilirrubina. Si el nivel de bilirrubina es elevado o se eleva rápidamente necesitará tratamiento lo más pronto posible.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5",attrs:{"data-aos":"zoom-in"}},[_c('div',{staticClass:"col-lg-auto px-0 mb-4 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"380px"},attrs:{"src":require("@/assets/curso/tema2/img21.svg"),"alt":""}})])]),_c('div',{staticClass:"col-lg bg-c12"},[_c('div',{staticClass:"p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("La bilirrubina puede ser eliminada a través del hígado, riñones y materia fecal, en ese proceso el cuerpo permanecerá de color amarillo con mayor o menor intensidad. Pasado el tiempo no muy largo, dos semanas en la mayoría de los casos, máximo tres, el recién nacido volverá a tomar su color de piel normal.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-auto bg-c16"},[_c('div',{staticClass:"p-2",attrs:{"data-aos":"fade-right"}},[_c('h3',{staticClass:"text-white mb-0"},[_vm._v("Piel y cordón")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._